 <script>

   export let currentFrame;
   export let frameData;
   export let translator;
   export let currLang;

   let currentDate;

    $: {
       currentDate = frameData[currentFrame].date;
    }

 </script>

 <span class='animation-date'>
    {#if currentDate}
        {translator.translateDate(currentDate, currLang)}
    {/if}
 </span>

 <style>
   .animation-date {
      color: #2B2D42
   }
 </style>
  
